@charset "utf-8";
@import "variables";

// ==================================================
// 	media query
// ==================================================
@mixin media($media-width) {
	@if $media-width == sp {
		@at-root {/* ▽ SP layout ========== */}
		@media only screen and (max-width: $sp-dimension) {
			@content;
		}
		@at-root {/* ========== end △ */}
	}
	@else if $media-width == tab {
		@at-root {/* ▽ PC-TAB layout ========== */}
		@media only screen and (max-width: ($pc-dimension - 1)) and (min-width: $sp-dimension + 1) {
			@content;
		}
		@at-root {/* ========== end △ */}
	}
	@else if $media-width == pc {
		@at-root {/* ▽ PC-TAB layout ========== */}
		@media only screen and (min-width: $pc-dimension) {
			@content;
		}
		@at-root {/* ========== end △ */}
	}
	@else if $media-width == print {
		@at-root {/* ▽ PRINT layout ========== */}
		@media print {
			@content;
		}
		@at-root {/* ========== end △ */}
	}
}


// ==================================================
// 	clearfix
// ==================================================
@mixin clearfix() {
	&::after {
		content:"";
		display:block;
		clear:both;
	}
}

@function rem($pt) {
  @return #{((($pt * 300 )/72)/2)/10}rem;
}
