@charset "utf-8";
@import "variables";
@import "mixin";

/* ==================================================
	basic layout
================================================== */
* {
	box-sizing: border-box;
}

body {
	font-family: $fontbase;
	font-size: 1.4rem;
	color: $color-text-body;
}

a {
	text-decoration: none;
	color: $color-link;
}

h1 {
	@include media(pc) {
		font-size: 3em;
		margin-bottom: 30px;
	}
	@include media(tab) {
		font-size: 2.5em;
		line-height: 1.125em;
	}
	@include media(sp) {
		font-size: 2em;
		line-height: 1.25em;
	}
}

h2 {
	@include media(pc) {
		font-size: 2.25em;
		line-height: 1.25em;
		margin-bottom: 30px;
	}
	@include media(tab) {
		font-size: 2em;
		line-height: 1.25em;
	}
	@include media(sp) {
		font-size: 1.625em;
		line-height: 1.15384615em;
	}
}

h3 {
	@include media(pc) {
		font-size: 1.75em;
		line-height: 1.25em;
		margin-bottom: 30px;
	}
	@include media(tab) {
		font-size: 1.5em;
		line-height: 1.25em;
	}
	@include media(sp) {
		font-size: 1.375em;
		line-height: 1.13636364em;
	}
}

h4 {
	@include media(pc) {
		font-size: 1.125em;
		line-height: 1.22222222em;
		margin-bottom: 30px;
	}
	@include media(tab) {
		font-size: 1.125em;
		line-height: 1.22222222em;
	}
	@include media(sp) {
		font-size: 1.125em;
		line-height: 1.11111111em;
	}
}

blockquote {
	padding: 30px;
	border: 1px solid #666;
	position: relative;
	margin-bottom: 30px;
	&:before,
	&:after {
		font-family: 'Yu Gothic Medium';
		font-size: 4rem;
		line-height: 4rem;
		font-style: normal;
		font-weight: bold;
		width: 2rem;
		height: 2rem;
		background: #fff;
		position: absolute;
		color: #666;
		text-align: center;
	}
	&:before {
		content: '\201C';
		top: -6px;
		left: -6px;
	}
	&:after {
		content: '\201D';
		bottom: -6px;
		right: -6px;
	}
	p {
		margin-bottom: 0 !important;
	}
}


.container {
	max-width: 1000px;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	padding-left: 1rem;
	padding-right: 1rem;
	@include media(pc) {
		padding-left: 0;
		padding-right: 0;
	}
}

.l {
	&-no {
		&__padding {
			padding: 0;
		}
		&__margin {
			margin: 0;
		}
	}
	&-flex {
		display: flex;
		&__no-wrap {
			flex-wrap: nowrap;
		}
		&__wrap {
			flex-wrap: wrap;
		}
		&__spb {
			justify-content: space-between;
		}
	}
	&-block {
		display: block;
	}
	&-none {
		display: none;
	}
	&-inline {
		display: inline;
	}
}

/* ==================================================
	until
================================================== */
$margin-top: mt;
$margin-bottom: mb;
@for $i from 0 through 10 {
	// for each $col_#{i}
	.#{$margin-top}#{$i} {
		margin-top:#{$i * 5}px;
	}
	.#{$margin-bottom}#{$i} {
		margin-top:#{$i * 5}px;
	}
}
/* ==================================================
	l-pageTitle
================================================== */
.l-pageTitle {
	background-color: lighten(gray, 30);
	width: 100%;
	height: 300px;
	margin-bottom: 30px;
	justify-content: center;
	align-items: center;
	h1, h2 {
		margin-bottom: 0;
	}
}
/* ==================================================
	l-header
================================================== */
.l-header {
	padding: 1em;
	.logo {
		font-weight: bold;
		margin-bottom: 0;
		@include media(pc) {
			font-size: 3em;
		}
		@include media(tab) {
			font-size: 2.5em;
			line-height: 1.125em;
		}
		@include media(sp) {
			font-size: 2em;
			line-height: 1.25em;
		}
	}
	&__right {
		display: flex;
		align-items: center;
	}
	#gnav {
		ul {
			display: flex;
			li {
				margin-left: 20px;
				a {
					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}
}
/* ==================================================
	l-content
================================================== */
.l-content {
	.entry-content {
		p {
			margin-bottom: 30px;
		}
		ul {
			margin-bottom: 30px;
			padding-left: 20px;
			list-style: disc;
		}
		ol {
			margin-bottom: 30px;
			padding-left: 20px;
			list-style: decimal;
		}
	}
}
/* ==================================================
	l-footer
================================================== */
.l-footer {
	background-color: #000;
	color: #fff;
	padding: 50px 0;
	.copy {
		text-align: right;
	}
	&__content {
		display: flex;
		justify-content: space-between;
		margin-bottom: 30px;
	}
	.flogo {
		color: #fff;
		font-size: 3em;
		font-weight: bold;
	}
	#fnav {
		ul {
			display: flex;
			height: 100%;
			align-items: center;
			li {
				margin-left: 20px;
				a {
					color: #fff;
					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}
}


/* ==================================================
	l-column
================================================== */
.l-column {
	flex: 1 1 auto;
	padding-right: 30px;
}


/* ==================================================
	sidebar
================================================== */
.sidebar {
	flex: 0 0 250px;
	background-color: #eee;
}


/* ==================================================
	Breadcrumb
================================================== */
.c-breakcrumb {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 15px 0;
  margin: 30px 0;
}
@media only screen and (max-width: 639px) {
  .c-breakcrumb {
    padding: 15px 0;
    margin: 20px 0;
  }
}
.c-breakcrumb li {
  font-size: 11px;
  position: relative;
  margin: 0 10px;
  padding-right: 25px;
}
.c-breakcrumb li a {
  font-size: 1.2rem;
  text-decoration: none;
  color: #112942;
}
.c-breakcrumb li:not(:last-child):after {
  content: "";
  position: absolute;
  right: 0px;
  top: 50%;
  margin-top: -5px;
  width: 6px;
  height: 10px;
  background: url(../img/arrow-right.svg) center center no-repeat;
  background-size: cover;
}
.c-breakcrumb li span {
  font-size: 1.2rem;
  text-decoration: none;
  color: #333;
}
